import { types } from "../types/types";

const initialState = {
    modalOpenDoc: false,
    modalOpenBug: false,
}



export const uiReducer = ( state = initialState, action ) => {

    switch ( action.type ) {
        case types.uiOpenModalDoc:
            return {
                ...state,
                modalOpenDoc: true
            }

        case types.uiCloseModalDoc:
            return {
                ...state,
                modalOpenDoc: false
            }
        case types.uiOpenModalBug:
            return {
                ...state,
                modalOpenBug: true
            }

        case types.uiCloseModalBug:
            return {
                ...state,
                modalOpenBug: false
            }
        default:
            return state;
    }


}