import React from 'react';
import { useDispatch } from 'react-redux';
import { startLogin } from '../../actions/auth';
import { useForm } from '../../hooks/useForm';
import './login.css';

export const LoginScreen = ({history}) =>{

    const dispatch = useDispatch();

    const [ formLoginValues, handleLoginInputChange ] = useForm( {
        lEmail: '',
        lPassword:''
    } );

    const {lEmail, lPassword} = formLoginValues;
    
    const handleLogin = (e) =>{
        e.preventDefault();
        dispatch(startLogin(lEmail, lPassword));
    }
    
    return(
        <div className="fondo">
            <div className="container login-container mt-0 row">
                <div className="col-lg-3">
                </div>
                <div className="login-form-2 col-lg-4 camposlogin">
                    <form onSubmit={ handleLogin}>
                        <div className="form-group matilogo animate__animated animate__fadeInUp  ">
                            <img className=" animate__heartBeat" src="./mati.png" alt="CAYS" width="200" height="180"/>
                        </div>
                        <div className="form-group">
                            <input 
                                style={{textDecorationColor:'#fff00'}}
                                type="text"
                                className="form-control bg-transparent"
                                placeholder="tucorreo@mati.io"
                                name= "lEmail"
                                value= {lEmail}
                                onChange={handleLoginInputChange}
                            />
                        </div>
                        <div className="form-group">
                            <input
                                style={{textDecorationColor:'#555555'}}
                                type="password"
                                className="form-control bg-transparent"
                                placeholder="Contrasea"
                                name= "lPassword"
                                value= {lPassword}
                                onChange={handleLoginInputChange}
                            />
                        </div>
                        <div className="form-group offset-lg-5">
                            <input 
                                type="submit"
                                className="btnSubmit "
                                value="Login" 
                            />
                        </div>
                    </form>
                </div>
                <div className="col-lg-5 matiname">
                    <div className=" animate__animated animate__fadeInRight  ">
                        <img className="ml-4 MAS" src="./pixlr-bg-result (2).png"  width="280" height="180"/>
                        <h3 className="MAS">Manual Annotation System</h3>
                    </div>
                </div>
                <div className="col-lg-2">
                </div>
            </div>
        </div>     
    )
}